<template>
  <div class="container">
    <section id="user">
      <p>Your Sun:</p>
      <select name="user-sun" id="user-sun" v-model="user.sun">
        <option
          :value="zodiac.sign"
          v-for="zodiac in zodiacs"
          v-bind:key="zodiac.sign"
        >
          {{ zodiac.sign }}
        </option>
      </select>
      <p>Your Moon:</p>
      <select name="user-moon" id="user-moon" v-model="user.moon">
        <option
          :value="zodiac.sign"
          v-for="zodiac in zodiacs"
          v-bind:key="zodiac.sign"
        >
          {{ zodiac.sign }}
        </option>
      </select>
      <p>Your Rising:</p>
      <select name="user-rising" id="user-rising" v-model="user.rising">
        <option
          :value="zodiac.sign"
          v-for="zodiac in zodiacs"
          v-bind:key="zodiac.sign"
        >
          {{ zodiac.sign }}
        </option>
      </select>
    </section>
    <section id="partner">
      <p>Your Partner's Sun:</p>
      <select name="partner-sun" id="partner-sun" v-model="partner.sun">
        <option
          :value="zodiac.sign"
          v-for="zodiac in zodiacs"
          v-bind:key="zodiac.sign"
        >
          {{ zodiac.sign }}
        </option>
      </select>
      <p>Your Partner's Moon:</p>
      <select name="partner-moon" id="partner-moon" v-model="partner.moon">
        <option
          :value="zodiac.sign"
          v-for="zodiac in zodiacs"
          v-bind:key="zodiac.sign"
        >
          {{ zodiac.sign }}
        </option>
      </select>
      <p>Your Partner's Rising:</p>
      <select
        name="partner-rising"
        id="partner-rising"
        v-model="partner.rising"
      >
        <option
          :value="zodiac.sign"
          v-for="zodiac in zodiacs"
          v-bind:key="zodiac.sign"
        >
          {{ zodiac.sign }}
        </option>
      </select>
    </section>
    <div class="break"></div>
    <button id="button" v-on:click="getCompatibility()">
      View Compatibility
    </button>
    <Loading v-if="isLoading"></Loading>
  </div>
</template>

<script>

import BigThreeService from "../service/BigThreeService";
import Loading from "./Loading.vue";

export default {
    name: "big-three",
    components: {
      Loading
    },
    data(){
        return{
            isLoading: false, 
            user: {
                sun: '',
                moon: '',
                rising: ''
            },
            partner: {
                sun: '',
                moon: '',
                rising: ''
            },
            zodiacs: [
            {sign: "Aries"},
            {sign: "Taurus"},
            {sign: "Gemini"},
            {sign: "Cancer"},
            {sign: "Leo"},
            {sign: "Virgo"},
            {sign: "Libra"},
            {sign: "Scorpio"},
            {sign: "Sagittarius"},
            {sign: "Capricorn"},
            {sign: "Aquarius"},
            {sign: "Pisces"}
    ]
        };

  },
  methods: {
    getCompatibility() { 
      this.isLoading = true;

      // eslint-disable-next-line no-global-assign
      prompt = `please give a 1-10 rating of overall compatibility, a brief summary of 
            friendship compatibility, and a brief summary of romantic compatibility based on 
            astrology between the two following people:  
            ${this.user.sun} sun, ${this.user.moon} moon, ${this.user.rising} rising 
            and ${this.partner.sun} sun, ${this.partner.moon} moon, ${this.partner.rising} rising. 
            Please do not provide an intro or outro, just the information i seek`;

      // console.log(prompt);


      BigThreeService.promptAstroChat(prompt)
        .then((response) => {
          this.$store.commit("SET_RESPONSE", response.data.content);
          //or this.$store.state.chatResponse = response.data; ?
          this.isLoading = false;
          this.$router.push({ name: "Compatibility" });
        })
        .catch((error) => {
           this.isLoading = false;
           console.log(error);
        });

        
    },
  },
};

</script>

