import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const currentChatResponse = JSON.parse(localStorage.getItem('chatResponse'));

export default new Vuex.Store({
    state: {
        chatResponse: currentChatResponse || '',
        
    },

    mutations: {
        SET_RESPONSE(state, chatResponse){
            state.chatResponse = chatResponse;
            localStorage.setItem('chatResponse', JSON.stringify(chatResponse))
        }
    }

    
})