<template>
  <div>
    <h3 v-for="chat in chats" v-bind:key="chat">{{ chat }}</h3>
   
    <!-- sections of response separated by \n\n -->
  </div>
</template>

<script>
export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name: "results",
    data(){
        return{
            chatResponse: '',
            chats: []
        }
    },
    created(){
        this.chatResponse = this.$store.state.chatResponse;
        this.chats = this.chatResponse.split("\n\n");
    }

}
</script>

